*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Quicksand', sans-serif;
  text-decoration: none;
}
:root {
    --carat: #EB6174;
    /* #EB6174; */
}
 a{
    color: white;
 }
  body{
    width: 100%;
    height: 100vh;
  }

  nav{
    width: 100%;
    height: 60px;
    background-color: var(--carat);
  }
#logo{
 color: rgb(255, 255, 255);
    font-size: 2rem;
    line-height: 3rem;
    position: absolute;
    left: 56px;
    padding: 3px;
} 
#logos{
  top: 5px;
    left: -49px;
    width: 50px;
    height: 50px;
    position: absolute;
}
   nav ul {
   margin: 18px;
    float: right;
    display: flex;
   }

   nav ul li {
    margin-left: 1.9rem;
    list-style: none;
   }
   nav ul li a{
     line-height: 3px;
    padding: 6px 12px;
    text-decoration: none;
    font-size: 1.2rem;
    color: white;
    border-radius: 8px;

   }

   nav ul li a:hover {
    background-color: #ff002363;
}

.chekbox {
  color: white;
    font-size: 2rem;
    float: right;
    margin-right: 14px;
    line-height: 25px;
    margin-top: 19px;
    cursor: pointer;
    display: none;

}

#check {
    display: none;


}
/* Main Header Pic Content */
  .main_header{
   margin-top: 30px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-content: stretch;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
  }
 .main_header img{
      width: 500px;
    height: 400px;
    filter: drop-shadow(2px 4px 6px black);
 }
 .main_content {
    margin-top: 10px;
   
 }
 .Pink_bg{
    top: 129px;
    left: 275px;
    width: 400px;
    height: 400px;
    position: absolute;
    
    filter: blur(41px);
    border-radius: 50%;
    background-color: #edb5bf;
    z-index: -1;
    opacity: .4;
}
 .main_content h1 b{
    position: relative;
    top: 20px;
   padding: 2px;
    text-transform: capitalize;
    font-size: 3rem;
    color: #f53f58;
    font-family: 'Glory', sans-serif;

 }
.main_content h2,h3{
      position: relative;
    text-align: center;
    font-size: 1.5rem;
    color: #ef93a0;
    top: 18px;
font-family: 'Overpass', sans-serif;

 }

/*End Main Section */
/* Start Card Section */
 .Parent_class{
     width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 68px;
    gap: 20px;
 }
 .child_class{
  width: 300px;
  height: 350px;
  background-color:#EB6174 ;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10px;
 }
 .child_class img{
   width: 200px;
   height: 150px;
   border-radius: 10px;
 }
/* End Card Section */
/* Start Love  Section */

 .Love_class{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
 }
 .love_child{
     width: 91%;
    height: 80vh;
    background-color: var(--carat);
    margin-top: 100px;
    border-radius: 10px;
    display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
 }
 .box{
 width: 375px;
    height: 414px;
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10px;
    margin: 20px;
 }
 .box img{
      width: 227px;
    height: 187px;
    border-radius: 10px;
 }
 .box h1{
          font-size: 1.8rem;
    color: #eb6174;
    padding: 2px;

 }
 .box p{
     padding: 5px;

 }
 .para2{
           color: white;
    padding: 10px;
    line-height: 1.4;
 }
 .child_class h3{
      padding: 4px;
    color: #ffff;
    margin-top: 5px;
 }
 .btn2{
    padding: 7px 12px;
    border-radius: 10px;
    color: black;
    width: 100%;
    font-size: 1rem;
    width: 100%;
    font-size: 1rem;
    text-decoration: none;
    background-color: #e84a5f;
    color: white;
    font-weight: bold;
    border: 2px solid #477895;
 cursor: pointer;
 }

/* End Love  Section */

/* Form Box Start */
.back{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
}
.back img {
   width: 200px;
   height: 150px;
}


.form{
  display: flex;
  flex-direction: column;
}
.bgbr{
  width: 501px;
    height: 362px;
    background-color: #ea6174;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5%;
    box-shadow: 10px 10px 5px 0px rgb(0 0 0 / 75%);
    -webkit-box-shadow: 10px 10px 5px 0px rgb(0 0 0 / 75%);
    margin: 20px;
    margin-bottom: 50px;

}
.cen{
 width: 469px;
    height: 332px;
    background-color: #ffdfea;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cen #nam {
    font-size: 1.6rem;
    padding: 2px;
}

.cen #ph {
    font-size: 1.6rem;
    padding: 2px;
}

.cen #mes {
    font-size: 1.6rem;
    padding: 2px;
}

#tex {
    height: 1.8rem;
    width: 226px;
    border-radius: 5px;
    border: 2px solid #EB6174;
}

.cen #pht {
    height: 1.8rem;
    border-radius: 5px;
    border: 2px solid #EB6174;
}

.cen #mest {
    height: 3.6rem;
    border-radius: 5px;
    border: 2px solid #EB6174;
}

#submit {
    width: 86px;
    height: 36px;
    color: #ffff;
    background-color: #e94a5f;
    border: 2px solid #4a7d9b;
    border-radius: 8px;
    cursor: pointer;
    margin-top: 13px;
}
.back_flex{
    width: 20rem;
    margin: 20px;

}
.back_flex h1{
       padding: 5px;
    font-size: 1.2rem;
    color: #EB6174;

}
.back_flex span{
       line-height: 2px;
    padding: 5px;
    font-size: 1rem;
    word-spacing: 3px;
    font-weight: 400;

}
textarea{
    padding: 2px;
    resize: none;
}
/* END Form Box Start */
/* Footer Code Start */
footer {

    width: 100%;
    height: 200px;
    background-color: var(--carat);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: auto;
}

.col {
    width: 95%;
    height: 138px;
    background-color: #ffffff;
    border-radius: 40px;
}

footer ul li {
        margin: 2.5% 0 0 5%;
    display: inline-block;
    font-size: 2.5rem;
}
 .col h1{
    line-height: 120px;
    text-align: center;
    color: #eb6174;
 }


/* Media Query For Mobile Phone */

 @media(max-width: 786px){


   
      
 

    nav ul li {
        display: block;
    }

    nav ul li a {
        font-size: 20px;
    }

  
    #logo{
          color: rgb(255, 255, 255);
    font-size: 2rem;
    line-height: 3rem;
    }
    .main_header img {
    width: 200px;
    height: 200px;
}
.love_child{
  flex-direction: column;
      height: auto;
 }
 .box{
  width: 300px;
   height: 350px;
 }
 .back {
  flex-direction: column;
 }
 .bgbr{
 width: 359px;
    height: 333px;
 }
 .cen{

 width: 297px;
    height: 282px;
 }
  .Pink_bg{
        width: 373px;
    height: 238px;
    left: 0;
   
    top: 65px;
}
}

/* Media Query For Mobile Phone */